<template>
	<div class="news">
		<div class="newsNav">
			<div v-for="(item,index) in navList" :key="index" @click="switchNav(index)">
				<span>{{item.title}}</span>
				<div class="line" v-show="active == index"></div>
			</div>
		</div>
		<div class="newsListBox">
			<img :src="require('@/assets/flower.png')" />
			<div v-show="active === 0">
				<div class="list" v-for="(item,index) in newList" :key="index">
					<span>{{ `${item.address.slice(0, 5)}...${item.address.slice(-5)}` }}</span>
					<span>{{item.amount.fromWei().toFixed(2)}} ETH</span>
					<!-- <span>{{toTime(item.time,'Y-M-D')}}</span> -->
				</div>
			</div>
			<div v-show="active === 1">
				<div class="list" v-for="(item,index) in luckyList" :key="index">
					<span>{{ `${item.address.slice(0, 5)}...${item.address.slice(-5)}` }}</span>
					<span>{{item.amount.fromWei().toFixed(2)}} ETH</span>
					<span>{{toTime(item.time,'Y-M-D')}}</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default{
		data(){
			return {
				navList: [
					{title: '最新参与'},
					{title: '幸运奖励'},
				],
				active: 0,
				newList: [],
				luckyList: [],
			}
		},
		methods:{
			switchNav(index){
				this.active = index
			},
			async getEthHistory(){
				let filterType = this.$web3.eth.abi.encodeParameter('int',-1),
					fromBlock = await this.$web3.eth.getBlockNumber().then() - (7200 * 7);
				axios.get('https://api-cn.etherscan.com/api',{
					params: {
						module: 'logs',
						action: 'getLogs',
						fromBlock: fromBlock,
						toBlock: 'latest',
						address: this.$contract.get('Main')._address,
						topic0: '0x9359efc2280a5a4ebb4622668f984d962b7cab782df7192df4ab125b7fa89610',
						topic3: filterType
					}
				}).then(res => {
					res.data.result.forEach(item=>{
						let address = this.$web3.eth.abi.decodeParameter('address',item.topics[1])
						let time = this.$web3.eth.abi.decodeParameter('uint',item.topics[2])
						let ptype = this.$web3.eth.abi.decodeParameter('int',item.topics[3])
						let amount = this.$web3.eth.abi.decodeParameter('uint',item.data)
						
						this.newList.unshift({
							address: address,
							amount: amount,
							time: time
						})
					})
					// console.log(res)
				})
			// 	this.$contract.get('Main').events.Event_EtherHistory({
			// 		fromBlock: 0,
			// 		filter: {ptype: '-1',time: this.time}
			// 	}, function(error, event){ })
			// 	.on('data', res => {
			// 		this.newList.push({
			// 			address: res.returnValues.owner,
			// 			amount: res.returnValues.amount,
			// 			time: res.returnValues.time
			// 		})
			// 	})
			// 	.on('changed', function(event){
			// 	})
			// 	.on('error', console.error);
			},
			getLuckyList(){
				this.$contract.get('SmallLuckPool').events.EventLuckdogHisotry({
					fromBlock: 0,
					filter: {time: this.time}
				},(err,res)=>{
					// console.log(res)
					this.luckyList.push({
						address: res.returnValues.owner,
						amount: res.returnValues.reward,
						time: res.returnValues.time
					})
					
					if(res.returnValues.owner.toLowerCase() == this.$root.address.toLowerCase()){
						this.$emit('showLucky',res.returnValues.reward.fromWei().toFixed(2))
					}
				})
			}
		},
		async mounted() {
			this.time = await this.currentTime('eight')
			this.getEthHistory()
			this.getLuckyList()
		}
	}
</script>

<style scoped lang="scss">
	.news{
		margin: 60px 0 96px;
		font-size: 28px;
		color: #E4E6E7;
		padding: 0 20px;
		
		.newsNav{
			display: flex;
			justify-content: space-around;
			margin-bottom: 38px;
			
			&>div{
				position: relative;
				line-height: 1.5;
				
				&>.line{
					position: absolute;
					width: 110%;
					// height: 0px;
					border-bottom: 1px solid #DDA33F;
					bottom: -8px;
					left: 50%;
					transform: translateX(-50%);
				}
			}
		}
		
		.newsListBox{
			width: 100%;
			height: 369px;
			background: #121212;
			border-radius: 20px;
			padding: 10px 30px 0 20px;
			display: flex;
			flex-direction: column;
			align-items: center;
			
			&>img{
				width: 238px;
				margin-bottom: 26px;
			}
			
			&>div{
				width: 100%;
				flex: 1;
				overflow: scroll;
				
				&>div{
					display: flex;
					justify-content: space-between;
					
					&>span{
						flex: 1;
					}
					
					&>span:nth-child(1){
						text-align: left;
					}
					
					&>span:nth-child(2){
						// text-align: right;
						color: #DDA33F;
					}
					
					&>span:nth-child(3){
						text-align: right;
					}
				}
			}
			
			&>div:nth-child(2){
				&>div{
					&>span:nth-child(2){
						text-align: right!important;
					}
				}
			}
		}
	}
</style>
